export default function Projects({
  projectTitle,
  projectDate,
  projectDesc,
  projectTech
}) {
  let techList = [];
  projectTech.forEach(item => techList.push(item));
  return (
    <div className="no-border">
      <h3>{projectTitle}</h3>
      <p>
        <span class="date">{projectDate}</span>
        <br />
        {projectDesc}
      </p>
      <ul>
        <lh>Built with</lh>
        {techList}
      </ul>
    </div>
  );
}
